.profile-banner {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding: 30px 30px;
  background-image: url("../../../assets/profileBanner3.jpg");
  background-size: cover;
  background-position: center;
  color: white;
  height: 230px;
}

.profile-info {
  display: flex;
  align-items: center;
}

.profile-image {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-right: 15px;
}

.user-details h2 {
  margin: 0 0 5px;
  font-size: 20px;
}

.user-details p {
  font-size: 16px;
  margin: 0;
}

.stats-box {
  display: flex;
  align-items: center;
  gap: 2rem;
  background-color: white;
  color: black;
  padding: 10px 15px;
  border-radius: 5px;
  margin-top: 10px;
}

.stats-box p {
  display: flex;
  align-items: center;
  margin: 0;
  font-size: 18px;
}

.stats-box p span {
  margin-left: 5px;
  font-weight: bold;
}

.stats-box p svg {
  color: gold;
  font-size: 20px;
}

.logout-button {
  background-color: var(--color-g5);
  color: white;
  border: none;
  padding: 14px 21px;
  border-radius: 20px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.logout-button:hover {
  background-color: #354547;
}

@media (max-width:900px) {
  .profile-banner{
    padding: 20px 6px;
    justify-content: space-between;
    align-items: flex-end;
  }
  .profile-image{
    width: 60px;
    height: 60px;
    align-self: flex-end;
  }
  
  
  
  .stats-box{
    padding: 10px 9px;
    gap: 12px;
  }
}