.singleQuestionPage {
  padding: 20px;
  background-color: #f0f0f0;
  min-height: 100vh;
}

.question-container {
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  padding: 20px;
  margin-bottom: 30px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.singleQuestionPage .answersContainer {
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  padding: 20px;
  display: block;
  
}

.no-question {
  text-align: center;
  margin-top: 50px;
  font-size: 24px;
  color: #777;
}

@media (max-width: 768px) {
  .singleQuestionPage {
    padding: 10px;
  }

  .question-container {
    padding: 15px;
    margin-bottom: 20px;
  }
  .singleQuestionPage .answersContainer{
    max-width: 98%;
  }
}
